nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: black;
  padding: 20px 80px;
}
#navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}
#navbar li {
  list-style: none;
  padding: 5px;
  position: relative;
}
#navbar li a {
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 600;
  color: whitesmoke;
}
#navbar li a:hover,
#navbar li a:after {
  color: green;
}
#navbar li a:hover::after,
#navbar li a.active::after {
  content: "";
  width: 50%;
  height: 2px;
  background: green;
  position: absolute;
  bottom: 1px;
  left: 20px;
}
#mobile i {
  color: red;
  column-gap: 55px;
}
#mobile {
  display: none;
}
@media screen and (max-width: 769px) {
  #navbar {
    display: flex;

    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    position: fixed;
    top: 90px;
    right: -300px;
    width: 300px;
    height: 100vh;
    background: #2a3239;
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0) 0.1;
    padding: 40px 0 0 10px;
    transition: 0.3s;
  }
  #mobile {
    display: block;
  }
  #navbar li {
    margin-bottom: 25px;
  }
  #mobile {
    font-size: 24px;
    cursor: pointer;
  }
  #navbar.active {
    right: 0px;
  }
}
